import React, { Fragment, useEffect } from "react";
import { Containers } from "@/containers";
import { scroller } from "react-scroll";
import { Helmet } from "react-helmet-async";
import { metaTag } from "@/src/meta-Seo";

export default function Home({ layout: { components = [] }, ...restProps }) {
  useEffect(() => {
    if (window?.location) {
      scroller.scrollTo(window?.location?.hash, {
        duration: 800,
        delay: 0,
        smooth: "easeInOutQuart",
      });
    }
  }, []);

  return (
    <Fragment>
      <Helmet>
        <title>{metaTag["home"]["title"]}</title>
        <meta name="description" content={metaTag["home"]["desc"]} />
        <link rel="canonical" href={metaTag["home"]["url"]} />
      </Helmet>
      {Object.entries(components).map(([key, value]) => {
        let { data = "{}", type = "", componentId = "" } = value;
        var prop_data = {
          data: JSON.parse(data),
          type: type,
          id: componentId,
        };
        const LayoutContainers =
          Containers[type.replace("Component", "Container")];
        return LayoutContainers ? (
          <LayoutContainers key={key} {...prop_data} {...restProps} />
        ) : null;
      })}
    </Fragment>
  );
}
