import StorageUtils from "./storage.util";

export const curruncySuffix = (labelValue) => {
  // Nine Zeroes for Billions
  return Math.abs(Number(labelValue)) >= 1.0e9
    ? Math.abs(Number(labelValue)) / 1.0e9 + " Billion "
    : // Six Zeroes for Millions
    Math.abs(Number(labelValue)) >= 1.0e6
    ? Math.abs(Number(labelValue)) / 1.0e6 + " Million "
    : // Three Zeroes for Thousands
      // : Math.abs(Number(labelValue)) >= 1.0e+3

      // ? Math.abs(Number(labelValue)) / 1.0e+3 + "K"

      Math.abs(Number(labelValue));
};

export const addComma = (value) => {
  return value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") == 0 ||
    value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") == "NaN"
    ? 0
    : value?.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
};

export const validateEmail = (email) => {
  let re =
    /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(String(email).toLowerCase());
};

export const priceCount = (count, type, shareType) => {
  if (type == "Cow" && shareType == "full") {
    return count * 7;
  }
  return count;
};

export const getProjectId = (id) => {
  const programs = StorageUtils.getItem('programs')
  if(programs){
      let program =  programs.filter(i=> i.id == id || i.shortDescription == id)
     let  projectId = program[0].id
   let title = program[0].shortDescription
      return { projectId, title };
  }
  return { projectId : null, title : null };
};
