import React, { Fragment, useEffect } from "react";
import { Containers } from "@/containers";
import { get } from "@/lib/http-client";
import useSWR from "swr";
import { LAYOUT_URL_PROJECT } from "@/utils/urls.utill";
import { useNavigate, useParams } from "react-router-dom";
import Helmet from "react-helmet";
import { metaTag } from "@/src/meta-Seo";
import { getProjectId } from "@/utils/helper";

export default function Projects({ ...restProps }) {
  const { data } = useSWR(LAYOUT_URL_PROJECT, get);
  let { components } = data || {};
  let { id } = useParams();
   const { projectId, title } = getProjectId(id) 
   const navigate = useNavigate()

  const Redirect = () => {
    if(title)
      navigate(`/${title}`)
  }

  useEffect(()=>{
    if(id)
      Redirect()
  },[id])

  return (
    projectId &&
    <Fragment>
      <Helmet>
        <title>{metaTag["project"]?.[projectId]["title"]}</title>
        <meta name="description" content={metaTag["project"]?.[projectId]["desc"]} />
        <link rel="canonical" href={metaTag["project"]?.[projectId]["url"]} />
      </Helmet>
      {components &&
        Object.entries(components).map(([key, value]) => {
          let { data = "{}", type = "", componentId = "" } = value;
          var prop_data = {
            data: JSON.parse(data),
            type: type,
            id: componentId,
          };
          const LayoutContainers =
            Containers[type.replace("Component", "Container")];
          return LayoutContainers ? (
            <LayoutContainers key={key} {...prop_data} {...restProps} />
          ) : null;
        })}
    </Fragment>
  );
}
