import React from "react";
import AppCarousel from "@/components/slide/Carousel";
export default function CarouselContainer({
  data: { apiUrl = false } = {},
  ...restProps
}) {
  if (apiUrl)
    return (
      <AppCarousel
        sliderUrl={apiUrl}
        carousel={true}
        slideClass="max-h-95vh"
        options={{
          type: "loop",
          waitForTransition: true,
          perPage: 1,
          width: "100%",
          autoplay: true,
          interval: "5000",
          pauseOnHover: true,
          pauseOnFocus: false,
        }}
        slideComponent="MainCarouselSlide"
        leftArrowClass={"mc-left-arrow-class"}
        leftArrowParenClass={"mc-left-arrow-parent-class"}
        rightArrowClass={"mc-right-arrow-class"}
        rightArrowParenClass={"mc-right-arrow-parent-class"}
        {...restProps}
      />
    );
  else return null;
}
