import Section from "@/components/ui/Sections/Section";
import { Containers } from "@/containers/";

const Colum = ({ data = false }) => {
  var GetCol = Containers[data?.data?.container];
  if (GetCol) {
    return <GetCol {...data} />;
  } else {
    return <></>;
  }
};

export default function TwoColContainer({ ...restProps }) {
  const data = restProps.data;
  const componentId = restProps.id;
  return (
    <Section>
      <div
        id={componentId}
        className="flex w-full flex-col md:flex-row lg:space-x-4"
      >
        <Colum
          data={{ data: data[0] }}
          tag={data[0]?.tag}
          title={data[0]?.title}
          link={data[0]?.button?.action?.payload}
          text={data[0]?.button?.title}
        />
        <Colum
          data={{ data: data[1] }}
          tag={data[1]?.tag}
          title={data[1]?.title}
          link={data[0]?.button?.action?.payload}
          text={data[0]?.button?.title}
        />
      </div>
    </Section>
  );
}
