import Copyrights from "@/components/ui/Copyrights";
import FooterContainer from "@/containers/footer";

export default function AppFooter({ footer = false, ...restProps }) {
  const navContent = footer;
  let { key, menus, sub_footer } = navContent || {};
  return footer ? (
    <>
      <FooterContainer id={key} menus={menus} sub_footer={sub_footer} />
      {/* <Copyrights /> */}
    </>
  ) : null;
}
