import SectionCard from "@/components/ui/Sections/SectionCard";
import Section from "@/components/ui/Sections/Section";
import AppCarousel from "@/components/slide/Carousel";
import { useState } from "react";
export default function OneColContainer({ ...restProps }) {
  const data = restProps.data;
  const [size, setSize] = useState(data.perPage);
  const [page, setPage] = useState(0);
  const componentId = restProps.id;
  const changePage = (next) => {
    if (next) {
      setPage(page + 1);
    } else {
      setPage(page - 1);
    }
  };
  return (
    <Section id={componentId}>
      <SectionCard
        title={data.title}
        tag={data.tag}
        link={data?.button?.action?.payload}
        text={data?.button?.title}
        {...restProps}
      >
        <div className="flex items-end justify-between">
          <p className="text-sm leading-loose text-justify text-gray-500">
            {data.description}
          </p>
          <div className="w-1/6 text-right"></div>
        </div>
        <div className=" w-full mt-4  overflow-x-auto ">
          <AppCarousel
            slideClass=""
            sliderUrl={data.apiUrl + "?page=" + page + "&size=" + size + ""}
            carousel={true}
            changePage={changePage}
            options={{
              type: "slide",
              perPage: size,
              width: "100%",
              gap: "0.3em",
              autoplay: true,
              interval: "1000",
              pauseOnHover: true,
              pauseOnFocus: true,
              breakpoints: {
                640: {
                  perPage: 1,
                  rewind: true,
                  interval: "5000",
                  type: "slide",
                },
              },
            }}
            slideComponent={"CardCarouselSlide"}
            cardName={data.card_type}
            leftArrowClass={"pp-left-arrow-class"}
            leftArrowParenClass={"pp-left-arrow-parent-class"}
            rightArrowClass={"pp-right-arrow-class"}
            rightArrowParenClass={"pp-right-arrow-parent-class"}
            {...restProps}
          />
        </div>
      </SectionCard>
    </Section>
  );
}
