import React from "react";
import SectionTitle from "@/components/ui/Sections/SectionTitle";
import CardImage from "@/components/ui/Cards/CardImage";
import { UrlUtils } from "@/utils/urls.utill";
import parse from "html-react-parser";

export default function AboutContainer({ ...restProps }) {
  const data = restProps.data;
  const componentId = restProps.id;

  return (
    <section id={componentId} className="bg-white  ">
      <div className="container flex flex-col-reverse w-full max-w-6xl lg:py-16 sm:py-8 px-2  mx-auto md:flex-row sm:px-4 xl:px-0">
        {/* <!-- Content --> */}
        <div className="flex-1 order-1 h-full mt-6 space-y-8 md:mt-0">
          <SectionTitle>{data?.title}</SectionTitle>
          <p className="pr-6 text-sm leading-loose text-justify text-gray-500">
            {parse(data?.description)}
          </p>
          {data?.button && (
            <a
              href="#"
              className="inline-block px-6 py-3 text-sm text-white bg-bsRed-500"
            >
              {data.button.title}
            </a>
          )}
        </div>
        {data?.imgUrl && <CardImage img={UrlUtils.assetsUrl + data.imgUrl} />}
      </div>
    </section>
  );
}
