import React, { useLayoutEffect, useState } from "react";
import { UrlUtils } from "@/utils/urls.utill";
import { TextLoader } from "@/components/_contentLoader";
import { useGetHook } from "@/hooks/getHook";
import SectionCard from "@/components/ui/Sections/SectionCard";

export default function ProjectContainer({
  tag = "",
  button: { action: { payload = "" } = {}, title = "" } = ({} = {}),
  ...restProps
}) {
  const useWindowSize = () => {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth, window.innerHeight]);
      }
      window.addEventListener("resize", updateSize);
      updateSize();
      return () => window.removeEventListener("resize", updateSize);
    }, []);
    return size;
  };

  const [width] = useWindowSize();
  const { response, isLoading, error } = useGetHook(restProps?.data?.apiUrl);

  if (error) return null;
  if (isLoading) return <TextLoader />;
  else {
    var response_data = JSON.parse(response?.data || "{}");
    return (
      <SectionCard
        tag={tag}
        title={restProps?.data?.title}
        link={payload}
        text={title}
        {...restProps}
      >
        {isLoading ? (
          <TextLoader />
        ) : (
          <a
            href={response_data?.redirectUrl || "#"}
            target="_blank"
            rel="noreferrer"
            className="relative block p-3 bg-gray-800"
          >
            <div className="border ">
              <img
                loading="lazy"
                src={
                  width > 800
                    ? UrlUtils.assetsUrl + response_data?.imgUrl
                    : UrlUtils.assetsUrl +
                      response_data?.imgUrl.split(".").join("-mobile.")
                }
                alt=""
                className="w-full h-full border-8 border-white"
              />
            </div>
            <p className="pb-10 mt-4 py-6 text-sm text-justify text-white text-ellipsis">
              {response_data?.description}
            </p>
            <div className="absolute bottom-0 right-0 px-3 py-2 mr-4 text-white rounded-t-full bg-bsRed-500">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="w-6 h-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M14 5l7 7m0 0l-7 7m7-7H3"
                />
              </svg>
            </div>
          </a>
        )}
      </SectionCard>
    );
  }
}
