import React, { useLayoutEffect, useState } from "react";
import { navData } from "@/lib/layout-handler";
import Logo from "@/components/_logo";
import MainHeader from "@/components/_mainHeader";
import SubHeader from "@/components/_subHeader";

export default function HeaderMenu({ header = false, ...restProps }) {
  const navContent = navData(header);
  const [menuShow, setMenuShow] = useState(false);
  const menuDropDown = () => setMenuShow(!menuShow);

  const useWindowSize = () => {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth, window.innerHeight]);
      }
      window.addEventListener("resize", updateSize);
      updateSize();
      return () => window.removeEventListener("resize", updateSize);
    }, []);
    return size;
  };

  return header ? (
    <header className="top-0 z-50 flex justify-center w-full">
      <div className="flex items-center w-full h-16 bg-gray-50 shadow-md md:h-28">
        <Logo navContent={navContent} />
        <div className="flex flex-col w-full h-full">
          <MainHeader
            navContent={navContent}
            menuShow={menuShow}
            useWindowSize={useWindowSize}
            menuDropDown={menuDropDown}
          />
          <SubHeader
            navContent={navContent}
            menuDropDown={menuDropDown}
            useWindowSize={useWindowSize}
          />
        </div>
      </div>
    </header>
  ) : null;
}
