import { Splide, SplideSlide } from "@splidejs/react-splide";
import "@/styles/Carousel.css";
import { useGetHook } from "@/hooks/getHook";
import { TextLoader } from "@/components/_contentLoader";
import { ChevronRightIcon, ChevronLeftIcon } from "@heroicons/react/solid";
import { Slides } from "@/components/slide";
import { useNavigate } from "react-router-dom";
import { useLayoutEffect, useState } from "react";
export default function AppCarousel({
  changePage = null,
  slideClass = "",
  sliderUrl = "",
  slideComponent = "",
  cardName = "",
  children,
  carousel = false,
  leftArrowClass = "",
  rightArrowClass = "",
  leftArrowParenClass = "",
  rightArrowParenClass = "",
  ...restProps
}) {
  let history = useNavigate();
  const { response, isLoading, error } = useGetHook(sliderUrl);
  let splids = response;
  if (response?.content) {
    splids = response?.content;
  }

  const useWindowSize = () => {
    const [size, setSize] = useState([0, 0]);
    useLayoutEffect(() => {
      function updateSize() {
        setSize([window.innerWidth, window.innerHeight]);
      }
      window.addEventListener("resize", updateSize);
      updateSize();
      return () => window.removeEventListener("resize", updateSize);
    }, []);
    return size;
  };

  const _renderControls = () => (
    <div className="splide__arrows">
      <div
        className={`splide__arrow--prev ${leftArrowParenClass} ${
          response?.first && "cursor-not-allowed"
        }`}
        role="button"
        {...(response?.first == false && {
          onClick: () => {
            changePage(false);
          },
        })}
      >
        <ChevronLeftIcon className={`w-full h-full ${leftArrowClass}`} />
      </div>

      <div
        className={`splide__arrow--next ${rightArrowParenClass} ${
          response?.last && "cursor-not-allowed"
        }`}
        role="button"
        {...(response?.last == false && {
          onClick: () => {
            changePage(true);
          },
        })}
        disabled="disabled"
      >
        <ChevronRightIcon className={`w-full h-full ${rightArrowClass}`} />
      </div>
    </div>
  );
  if (error || sliderUrl == "") return null;
  else {
    return isLoading ? (
      <TextLoader />
    ) : splids.length > 0 ? (
      <>
        {/* <div>
          Last Page: {response.last ? 'true': 'false'}
          <br/>
          First Page: {response.first ? 'true': 'false'}
          <br/>
          <br/>
          <br/>

        </div> */}

        <Splide
          renderControls={_renderControls}
          {...restProps}
          className={"h-full"}
        >
          {splids.map((i, e) => {
            console.log(i?.redirectUrl, "ii");
            const LayoutContainers = Slides[slideComponent];
            return (
              LayoutContainers && (
                <SplideSlide
                  onClick={() => {
                    !i?.redirectUrl && history(`project/${i?.id}`);
                  }}
                  className={slideClass}
                  key={e}
                >
                  {
                    <a
                      href={i?.redirectUrl && i?.redirectUrl}
                      target={i?.redirectUrl ? "_blank" : "_self"}
                      rel="noreferrer"
                    >
                      <LayoutContainers
                        index={e}
                        value={i}
                        cardName={cardName}
                        useWindowSize={useWindowSize}
                      />
                    </a>
                  }
                </SplideSlide>
              )
            );
          })}
        </Splide>
      </>
    ) : (
      <div className="h-20"></div>
    );
  }
}
