import React, { useEffect, useMemo, useState } from "react";
import { UrlUtils } from "@/utils/urls.utill";
import { TextLoader } from "@/components/_contentLoader";
import { useGetHook } from "@/hooks/getHook";
import SectionCard from "@/components/ui/Sections/SectionCard";
import useSWR from "swr";
import { get, post } from "@/lib/http-client";
import PaypalCheckoutButton from "@/components/PaypalCheckoutButton";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { DonationSuccess } from "@/components/DonationSuccess";
import { useNavigate } from "react-router-dom";

export default function DonationContainer({
  tag = "",
  button: { action: { payload = "" } = {}, title = "" } = ({} = {}),
  ...restProps
}) {
  const { response, isLoading, error } = useGetHook(restProps?.data?.apiUrl);
  var response_data = JSON.parse(response?.data || "{}");
  if (error) return null;
  if (isLoading) return <TextLoader />;
  else {
    var response_data = JSON.parse(response?.data || "{}");
    return (
      <SectionCard
        tag={tag}
        title={restProps?.data?.title}
        link={payload}
        text={title}
        {...restProps}
      >
        {isLoading ? (
          <TextLoader />
        ) : (
          <DonationContainerContent response_data={response_data} />
        )}
      </SectionCard>
    );
  }
}

const DonationContainerContent = ({ response_data, ...restProps }) => {
  const notify = (type, msg) =>
    toast[type](msg, {
      position: "top-center",
      autoClose: 5000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });

  const [userForm, setUserForm] = useState({
    donateFor: "",
    cause: "",
    amount: "",
  });

  const navigate = useNavigate();
  const [validation, setValidation] = useState(false);
  const [donationSuccess, setDonationSuccess] = useState(false);

  const onTextChange = (name, event) => {
    if (name == "donateFor") {
      const index = event.target.selectedIndex;
      const el = event.target.childNodes[index];
      const option = el.getAttribute("id");
      userForm[name] = `${option}-${event.target.value}`;
      validate();
    } else {
      userForm[name] = event.target.value;
      validate();
    }
  };

  let { donateAs, donateFor } = response_data;
  const {
    data: donatForAPI,
    error: error1,
    isLoading: isLoading1,
  } = useSWR(donateFor, get);
  const {
    data: donateAsAPI,
    error: error2,
    isLoading: isLoading2,
  } = useSWR(donateAs, get);

  const createDonation = (orderId) => {
    const body = {
      transactionNumber: orderId,
      notes: `${userForm["cause"]} ${userForm["donateFor"]}`,
      paymentMethod: "PAYPAL",
      currency: "USD",
      totalAmount: userForm["amount"],
      platform: "WEB",
      projectId: userForm["donateFor"]?.split("-")[0],
      donationType: userForm["cause"],
    };
    const headers = {};
    post(`${UrlUtils.apiUrl}donation/draft`, body, { headers })
      .then((response) => console.log(response))
      .catch((error) => {
        console.error("There was an error!", error);
      });
  };

  const confirmDonation = (payload) => {
    const headers = {};
    post(`${UrlUtils.apiUrl}donation/confirm`, payload, { headers })
      .then(
        (response) => {
          setUserForm({
            donateFor: "",
            cause: "",
            amount: "",
          });
          // navigate("/confirm-donation");
          // notify('success', 'Payment Has been Processed')
          setDonationSuccess(true);
        }
        // console.log(response)
      )
      .catch((error) => {
        console.error("There was an error!", error);
      });
  };

  const validate = () => {
    if (
      userForm.donateFor == "" ||
      userForm.cause == "" ||
      userForm.amount == ""
    ) {
      setValidation(false);
      return false;
    }
    setValidation(true);
    return true;
  };

  return !donationSuccess ? (
    <div
      className="flex-1 text-white bg-center bg-contain border bg-no-repeat"
      style={{
        backgroundImage: `url('${UrlUtils.assetsUrl + response_data?.imgUrl}')`,
      }}
    >
      <form
        action={response_data?.apiUrl}
        type={response_data?.formType}
        className="h-full p-4 space-y-4 bg-bsGreen-700/80"
        onSubmit={(event) => {
          event.preventDefault();
        }}
      >
        <div>
        <h2>Payment Type</h2>
          <div className="flex items-center mt-2 flex-col md:flex-row lg:space-x-20">
            <div className="flex items-center space-x-3">
              <input
                type="radio"
                name="paymentType"
                id="radio1"
                className="accent-bsGreen-500"
              />
              <label htmlFor="radio1">One Time</label>
            </div>

            <div className="flex items-center space-x-3">
              <input
                type="radio"
                name="paymentType"
                id="radio2"
                className="accent-bsGreen-500"
              />
              <label htmlFor="radio2">Recurring</label>
            </div>
          </div>
        </div>
        <div>
          <label htmlFor="dropdown1" className="block">
            I Want to Donate for
          </label>
          <select
            name="donateFor"
            id="dropdown1"
            className="w-full p-2 text-gray-800 bg-white"
            required
            onChange={(text) => onTextChange("donateFor", text)}
          >
            <option value="">Select cause for donation</option>
            {(donatForAPI?.content || []).map((i, e) => (
              <option key={e} value={i.value} id={i.id}>
                {i.title}
              </option>
            ))}
          </select>
        </div>
        <div>
          <label htmlFor="dropdown2" className="block">
            As
          </label>
          <select
            name="cause"
            id="dropdown2"
            className="w-full p-2 text-gray-800 bg-white"
            required
            onChange={(text) => onTextChange("cause", text)}
          >
            <option value="">Select cause first</option>
            {(donateAsAPI || []).map((i, e) => (
              <option key={e} value={i.value}>
                {i}
              </option>
            ))}
          </select>
        </div>
        <div>
          <label htmlFor="dropdown3" className="block">
            Amount I want to donate
          </label>
          {/* <select
                  name="amount"
                  id="dropdown3"
                  className="w-full p-2 text-gray-800 bg-white"
                >
                  {(response_data.amount || []).map((i, e) => (
                    <option key={e} value={i.value}>
                      {i.text}
                    </option>
                  ))}
                </select> */}
          <input
            name="amount"
            placeholder="Enter Amount"
            id="dropdown3"
            className="w-full p-2 text-gray-800 bg-white"
            required
            type={"number"}
            onChange={(text) => onTextChange("amount", text)}
          />
        </div>
        <div className="flex justify-end">
          {/* <button
                  className={`p-2 text-sm  ${response_data?.formButton?.type}-button uppercase`}
                  {...(response_data?.formButton?.behavior == "redirect" && {
                    onClick: () =>
                      (window.location.href = response_data?.formButton?.payload),
                  })}
                >
                  {response_data?.formButton?.title}
                </button> */}
          <button form="my-form" type="submit">
            <div
              style={{
                "pointer-events": validation ? "auto" : "none",
                opacity: validation ? "1" : "0.7",
              }}
            >
              <PaypalCheckoutButton
                fundingSource={"paypal"}
                confirmDonation={confirmDonation}
                notify={notify}
                formData={userForm}
                createDonation={createDonation}
              />
            </div>
          </button>
        </div>
      </form>
    </div>
  ) : (
    <DonationSuccess setDonationSuccess={setDonationSuccess} />
  );
};
