import React from "react";
import { HashLink } from "react-router-hash-link";
import { useNavigate } from "react-router-dom";
import useSWR from "swr";
import { UrlUtils } from "@/utils/urls.utill";
import { useGetHook } from "@/hooks/getHook";
import { get } from "@/lib/http-client";
import StorageUtils from "@/utils/storage.util";

export default function MainHeader({
  navContent,
  menuShow,
  useWindowSize,
  menuDropDown,
}) {
  let navigate = useNavigate();
  const [width, height] = useWindowSize();
  const menus = navContent.menus;
  const right_aligned_button = navContent.right_aligned_button;
  let { title = "", action: { behavior = "", payload = "#" } = {} } =
    right_aligned_button;
  return (
    <div
      className={`absolute sm:w-full right-0 flex flex-col items-center order-1 mt-16 mr-2 bg-white md:relative md:m-0 md:flex-row md:w-full ${
        width < 800 && !menuShow && "invisible"
      }`}
    >
      <nav className="z-50 flex flex-col items-center justify-center w-full text-sm md:h-12 md:flex-row bg-bsGreen-500">
        {(menus || []).map((i, e) => {
          let { title = "", action: { behavior = "", payload = "#" } = {} } = i;
          return (
            <div class="dropdown">
              <HashLink smooth to={`/#${payload}`}>
                <button
                  className="w-full h-full px-6 py-3 text-center text-white md:w-auto hover:text-bsGreen-500 hover:bg-white"
                  key={"nav.menus" + e}
                  {...(behavior == "redirect"
                    ? {
                        onClick: () => (window.location.href = payload),
                      }
                    : { onClick: menuDropDown })}
                >
                  <p className="uppercase">{title}</p>
                </button>
              </HashLink>
              <div
                class={`shadow-inner dropdown-content none absolute  min-w-[160px] z-1 bg-bsGreen-500 uppercase hover:text-bsGreen-500 ${
                  width < 800 && "relative "
                }`}
              >
                {i?.apiUrl && (
                  <SubMenuHeader
                    menuDropDown={menuDropDown}
                    item={i}
                    width={width}
                  />
                )}
              </div>
            </div>
          );
        })}
      </nav>
      {title && right_aligned_button?.action && (
        <button
          className="flex-shrink-0 w-full h-12 px-2 text-sm text-white md:w-36 bg-bsGreen-700 hover:bg-bsGreen-700/90 uppercase"
          {...(behavior == "redirect" && {
            onClick: () => (window.location.href = payload),
          })}
        >
          {title}
        </button>
      )}
    </div>
  );
}

const SubMenuHeader = ({ item, menuDropDown }) => {
  let { apiUrl } = item;
  const { data, loading, error } = useSWR(UrlUtils.baseUrl + apiUrl, get) || {};
  const { content } = data || [];
  content && StorageUtils.setItem("programs", content);
  let history = useNavigate();
  return data ? (
    (content || []).map((item) => (
      <div onClick={() => menuDropDown()}>
        <span
          onClick={() => history(`/${item?.shortDescription}`, { state: item })}
        >
          <p className="float-none pt-8 no-underline block text-center lg:text-left hover:bg-white  hover:text-bsGreen-500">
            {item.title}
          </p>
        </span>
      </div>
    ))
  ) : (
    <></>
  );
};
