import { UrlUtils } from "@/utils/urls.utill";
export const navData = (header) => {
  let logo = UrlUtils.assetsUrl + header.logo || "assets/logo.png";
  let menus = header?.nav?.menus || [];
  let right_aligned_button = header?.nav?.right_aligned_button || [];
  let sliders = header?.sub_header?.slider || "";
  let subheader_buttons = header?.sub_header?.buttons || [];

  return { logo, menus, right_aligned_button, sliders, subheader_buttons };
};
