export default function SectionBottomButton(props) {
  if (!props.link) return null;

  var btnclasses = "bg-white border-4 hover:border-bsRed-500 text-bsRed-500";
  if (props?.data?.button?.type == "filled") {
    btnclasses = props?.data?.button?.type + "-button";
  }

  return (
    <div className={`absolute bottom-0 flex justify-center w-full `}>
      <a
        href={props.link || "#"}
        className={`absolute p-2 -mt-5 text-sm uppercase ${btnclasses}`}
      >
        <span>{props.text || "Learn More"}</span>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          className="inline-block w-4 h-4 -mt-1"
          fill="none"
          viewBox="0 0 24 24"
          stroke="currentColor"
        >
          <path
            strokeLinecap="round"
            strokeLinejoin="round"
            strokeWidth="2"
            d="M13 5l7 7-7 7M5 5l7 7-7 7"
          />
        </svg>
      </a>
    </div>
  );
}
