import React from "react";
import { UrlUtils } from "@/utils/urls.utill";
import { TextLoader } from "@/components/_contentLoader";
import { useGetHook } from "@/hooks/getHook";
import SectionCard from "@/components/ui/Sections/SectionCard";
export default function SaibanContainer({
  tag = "",
  button: { action: { payload = "" } = {}, title = "" } = ({} = {}),
  ...restProps
}) {
  const { response, isLoading, error } = useGetHook(restProps?.data?.apiUrl);
  if (error) return null;
  if (isLoading) return <TextLoader />;
  else {
    var response_data = JSON.parse(response?.data || "{}");
    return (
      <SectionCard
        tag={tag}
        title={restProps?.data?.title}
        link={payload}
        text={title}
        {...restProps}
      >
        {isLoading ? (
          <TextLoader />
        ) : (
          <a
            href={response_data?.redirectUrl || "#"}
            className="relative block p-3 bg-gray-800"
          >
            <div className="border aspect-video">
              <img
              loading="lazy"
                src={UrlUtils.assetsUrl + response_data?.imgUrl}
                alt=""
                className="object-cover w-full h-full border-8 border-white"
              />
            </div>
            <p className="pb-10 mt-4 text-sm text-white text-ellipsis">
              {response_data?.description}
            </p>
            <div className="absolute bottom-0 right-0 px-3 py-2 mr-4 text-white rounded-t-full bg-bsRed-500">
              <svg
                xmlns="http://www.w3.org/2000/svg"
                className="w-6 h-6"
                fill="none"
                viewBox="0 0 24 24"
                stroke="currentColor"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M14 5l7 7m0 0l-7 7m7-7H3"
                />
              </svg>
            </div>
          </a>
        )}
      </SectionCard>
    );
  }
}
