import "@/styles/globals.css";
import { useLayout } from "@/hooks/layout";
import React from "react";
import { Routes, Route } from "react-router-dom";
import { Pages } from "@/src/pages";
import ProtectedRoutes from "@/components/ProtectedRoutes";
import Layout from "@/components/Layout";
import { HelmetProvider } from "react-helmet-async";
import { PayPalScriptProvider } from "@paypal/react-paypal-js";
import {
  PROD_PAYPAL_CLIENT_ID,
  DEV_PAYPAL_CLIENT_ID,
} from "./utils/urls.utill";

export default function App() {
  const { layout, isLoading, error } = useLayout();
  const getLayout = (page) => <Layout layout={layout}>{page}</Layout>;

  if (isLoading) return <p>Loading....</p>;
  else if (error) return <p>{error}</p>;
  else
    return (
      <PayPalScriptProvider options={{ "client-id": PROD_PAYPAL_CLIENT_ID }}>
        <HelmetProvider>
          <Routes>
            <Route
              path="/"
              element={getLayout(<Pages.Home layout={layout} />)}
            />
            <Route
              path="project/:id"
              element={getLayout(<Pages.Project layout={layout} />)}
            />
            <Route
              path="/:id"
              element={getLayout(<Pages.Project layout={layout} />)}
            />
            {/* <Route
              path="waqf-qurbani"
              element={getLayout(<Pages.WaqfQurabni layout={layout} />)}
            /> */}
            {/* <Route
              path="donation/id/:id"
              element={getLayout(<Pages.ConfirmDonation layout={layout} />)}
            /> */}
          </Routes>
        </HelmetProvider>
      </PayPalScriptProvider>
    );
}
