import SectionTitle from "@/components/ui/Sections/SectionTitle";
import { get } from "@/lib/http-client";
import { getProjectId } from "@/utils/helper";
import StorageUtils from "@/utils/storage.util";
import { UrlUtils } from "@/utils/urls.utill";
import parse from "html-react-parser";
import React from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";

export default function DescriptiveContainer({ ...restProps }) {
  let { id } = useParams();
  const { projectId  } = getProjectId(id)

    const {
    data: { apiUrl },
  } = restProps || "";
  const { data } = useSWR(
    `${UrlUtils.baseUrl + apiUrl.split("{")[0] + projectId}`,
    get
  );
  const { title, description, image } = data || {};
  const componentId = restProps?.id;

  return (
    <section id={componentId} className="bg-white">
      <img
        alt=""
        className="w-full h-full max-h-[95vh]"
        src={`${UrlUtils.assetsUrl + image}`}
      ></img>
      <div className="container flex flex-col w-full max-w-6xl px-2 py-16 pb-4 mx-auto md:flex-row sm:px-4 xl:px-0">
        <div className="flex-1 order-1 h-full mt-2 space-y-8 md:mt-0">
          <SectionTitle>{title}</SectionTitle>
          {description && parse(description)}
        </div>
      </div>
    </section>
  );
}
