import { get } from "@/lib/http-client";
import { addComma, curruncySuffix, getProjectId } from "@/utils/helper";
import StorageUtils from "@/utils/storage.util";
import { UrlUtils } from "@/utils/urls.utill";
import React from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";

export default function TabularContainer({ ...restProps }) {
  let { id } = useParams();
  const { projectId } = getProjectId(id)

  const { data } = useSWR(
    `${UrlUtils.baseUrl}/api/public/v1/programs/${projectId}/projects`,
    get
  );
  const { data: response } = useSWR(
    `${UrlUtils.baseUrl}/api/public/v1/programs/${projectId}`,
    get
  );
  let { tabularColumns } = response || [];

  let thead = tabularColumns?.split(",") || [];
  const componentId = restProps.id;
  return (
    <section id={componentId} className="bg-white">
      <div className="container flex flex-col w-full max-w-6xl px-2 py-16 pt-4  mx-auto md:flex-row sm:px-4 xl:px-0">
        <div className="flex-1 order-1 h-full mt-6 space-y-8 md:mt-0">
          {data && (
            <div style={{ "overflow-x": "auto" }}>
              <table
                className="projectTable"
                style={{
                  "border-collapse": "collapse",
                  "border-spacing": "0",
                  width: "100%",
                  border: "1px solid #ddd",
                }}
              >
                <tr>
                  {thead.map((item) => (
                    <th>{item}</th>
                  ))}
                </tr>
                {(data || []).map((item) => (
                  <tr>
                    {item?.description && (
                      <td className="pr-6 text-sm leading-loose text-justify text-gray-500">
                        {item?.description}
                      </td>
                    )}
                    {item?.targetBeneficiary && (
                      <td className="pr-6 text-sm leading-loose text-justify text-gray-500">
                        {item?.targetBeneficiary}
                      </td>
                    )}
                    {item?.currency && (
                      <td className="pr-6 text-sm leading-loose text-justify text-gray-500">
                        {item?.currency && item?.cost
                          ? `${item?.currency}$ ${addComma(
                              curruncySuffix(item?.cost)
                            )}`
                          : ""}
                      </td>
                    )}
                  </tr>
                ))}
              </table>
            </div>
          )}
        </div>
      </div>
    </section>
  );
}
