import React from "react";
import AppCarousel from "@/components/slide/Carousel";
import { HashLink } from "react-router-hash-link";
import { useGetHook } from "@/hooks/getHook";

export default function SubHeader({
  navContent,
  menuDropDown,
  useWindowSize,
  ...restProps
}) {
  const [width, height] = useWindowSize();
  const sliders = navContent.sliders;
  const { response } = useGetHook(sliders);
  const subheader_buttons = navContent.subheader_buttons;
  return (
    <div className="flex items-center w-full h-16 md:order-1 bg-gray-50">
      {width > 800 && (
        <div className="flex-1 h-full">
          {/* <AppCarousel
            sliderUrl={sliders}
            carousel={true}
            slideClass="h-full flex items-center justify-center"
            options={{
              type: "loop",
              waitForTransition: true,
              perPage: 1,
              width: "100%",
              autoplay: true,
              interval: "5000",
              pauseOnHover: true,
              pauseOnFocus: true,
            }}
            slideComponent="SubHeaderSlides"
            leftArrowClass={"sh-left-arrow-class"}
            leftArrowParenClass={"sh-left-arrow-parent-class"}
            rightArrowClass={"sh-right-arrow-class"}
            rightArrowParenClass={"sh-right-arrow-parent-class"}
            {...restProps}
          /> */}
          {response && (
            <marquee
              style={{ minWidth: "40rem" }}
              className="h-full flex items-center justify-center"
            >
              {response.map((i, index) => (
                <h1 key={index}>{` ${i.text} `}</h1>
              ))}
            </marquee>
          )}
        </div>
      )}
      <div
        className={`flex flex-shrink-0 p-4 space-x-2 ${
          width < 800 ? "w-full justify-end" : "w-auto"
        }`}
      >
        {subheader_buttons.map((i, e) => {
          let { title = "", action: { behavior = "", payload = "#" } = {} } = i;
          return (
            <HashLink smooth to={`${payload}`}>
              <button
                key={e}
                className={`p-2 text-sm  ${i.type}-button uppercase`}
                {...(behavior == "redirect" && {
                  onClick: () => (window.location.href = payload),
                })}
              >
                {title}
              </button>
            </HashLink>
          );
        })}
        <button
          className="p-2 border md:hidden border-bsGreen-500 text-bsGreen-500 hover:bg-bsGreen-500 hover:text-white"
          onClick={menuDropDown}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="w-6 h-6"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth="2"
              d="M4 6h16M4 12h16M4 18h16"
            />
          </svg>
        </button>
      </div>
    </div>
  );
}
